import React, { Component } from 'react';
import './Service.css';



class Service extends Component {
  render() {
    return (
      <div className="Service">
        <div className="Service-container">
          <div className="Service-logoContainer">
            <img className="Service-logo" src={this.props.service.icon} alt={this.props.service.title}></img>
            <p style={{ color: this.props.colorTitle }} className="Service-title">
              {this.props.service.title}
              <img className="Service-title-marco" src={this.props.marcoServices} alt={this.props.service.title}></img>
            </p>
          </div>
          <p style={{ color: this.props.color }} className="Service-description"> {this.props.service.description}</p>
        </div>
      </div>
    );
  }
}

export default Service;